.App {
  width: 100vw;
  height: 100vh;
}

body {
  margin: 0;
  pad: 0;
}

.Navbar {
  background-color: #021e39;
  width: 100%;
  height: 80px;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

.Navbar .leftSide {
  flex: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Navbar .leftSide .link {
  max-height: 230px;
}

.Navbar .leftSide .links a {
  text-decoration: none;
  color: antiquewhite;
  font-size: 25px;
  margin-left: 15px;
}

.Navbar .leftSide button {
  display: none;
  margin-left: 20px;
}

.Navbar .leftSide #hidden {
  display: flex;
}

.Navbar .rightSide {
  flex: 50%;
}

@media only screen and (max-width: 850px) {
  .Navbar .leftSide .links a {
    font-size: 20px;
  }
}

@media only screen and (max-width: 750px) {
  .Navbar .leftSide .links a {
    font-size: 15px;
  }
}

@media only screen and (max-width: 650px) {
  .Navbar .leftSide button {
    display: flex;
  }

  .Navbar .leftSide .links {
    display: none;
  }
}

.Navbar .leftSide #hidden {
  position: absolute;
  left: 0px;
  top: 80px;
  height: 230px;
  max-height: 230px;
  width: 100%;
  background-color: #021e39;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Navbar .leftSide #hidden a {
  margin: 5px;
}
